@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#test{
  position: absolute; top: 0px; left: 0px; width: 500px; height: 500px; 
}

.unity-canvas { position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; }

.App {
  text-align: center;
}


.unity-canvas-default {
  position: absolute;
  top: 5%;
  left: 20%;
  width: 60%;
  height: 60%;
}

.unity-canvas-zero {
  position: absolute;
  top: 0px; 
  left: 0px; 
  width: 0%; 
  height: 0%; 
}

.fake-unity-frame{
  position: absolute;
  top: 5%;
  left: 20%;
  width: 60%;
  height: 60%;
  background-color: #000;
  border:1px solid #000;
}

.btn-container{
  overflow: visible;
}

.btn { 
  position:absolute;
  background-color: black;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  left: 20%;
  top: 68%;
  cursor: pointer;
}

.btn1 { 
  position:absolute;
  background-color: black;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  left: 30%;
  top: 68%;
  cursor: pointer;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper{
  position: relative;
  padding-bottom: 56.25%;
  overflow: visible;
}


body{background-color:#2b2b2b;color:#fff;font-family:"Roboto",sans-serif}.container{max-width:500px;margin:50px auto}.container h1{text-align:center}.container .progressbar-container{position:relative;width:100%;height:50px;border:1px solid #fff;border-radius:10px;display:flex;align-items:center;justify-content:center;overflow:hidden}.container .progressbar-container .progressbar-complete{position:absolute;left:0;top:0px;height:100%;background-color:#5225bd;border-radius:10px;animation:g 2500ms infinite ease-in-out;z-index:2}.container .progressbar-container .progressbar-complete .progressbar-liquid{z-index:1;width:70px;height:70px;animation:g 2500ms infinite ease-in-out,r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);position:absolute;right:-5px;top:-10px;background-color:#5225bd;border-radius:40%}.container .progressbar-container .progress{z-index:2}@keyframes g{0%{background-color:#5225bd}50%{background-color:#607e9e}100%{background-color:#5225bd}}@keyframes r{from{transform:rotate(0deg)}from{transform:rotate(360deg)}}
