.App {
  text-align: center;
}


.unity-canvas-default {
  position: absolute;
  top: 5%;
  left: 20%;
  width: 60%;
  height: 60%;
}

.unity-canvas-zero {
  position: absolute;
  top: 0px; 
  left: 0px; 
  width: 0%; 
  height: 0%; 
}

.fake-unity-frame{
  position: absolute;
  top: 5%;
  left: 20%;
  width: 60%;
  height: 60%;
  background-color: #000;
  border:1px solid #000;
}

.btn-container{
  overflow: visible;
}

.btn { 
  position:absolute;
  background-color: black;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  left: 20%;
  top: 68%;
  cursor: pointer;
}

.btn1 { 
  position:absolute;
  background-color: black;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  left: 30%;
  top: 68%;
  cursor: pointer;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper{
  position: relative;
  padding-bottom: 56.25%;
  overflow: visible;
}

